<template>
  <div>
    <div class="form-container">
      <ProgressCircle :current-step="2" :total-steps="7"></ProgressCircle>
      <h2 class="form-heading">{{ $t('property.define_specs') }}</h2>
      <div class="form-input">
        <label>
          {{ $t('property.furnishing_status') }} <sup style="color: red">*</sup>
        </label>
        <ChipSelector
          value="id"
          label="text"
          @update-selection="updateFurnishType"
          :options="propertyData.furnishType"
          :defaultValue="property.form2.furnishType"></ChipSelector>
      </div>
      <div class="form-input">
        <label>
          {{ $t('property.property_types') }} <sup style="color: red">*</sup>
        </label>
        <ChipSelector
          value="prop_type_id"
          label="prop_type_name"
          @update-selection="updatePropertyType"
          :options="propertyData.propertyType"
          :defaultValue="property.form2.propertyType"></ChipSelector>
      </div>
      <div class="form-input area d-flex justify-content-between align-items-center">
        <label>
          {{ $t('property.propertyArea') }}
        </label>
        <AppInput
          type="number"
          :min="0"
          :max="10000"
          validation-rules="required|min:0|max:10000"
          input-id="area"
          :placeholder="dir === 'ltr' ? 'ex: 200m' : 'مثال: 200م'"
          :isRequired="false"
          v-model="property.form2.area"></AppInput>
          <span v-if="property.form2.area" :class="['metre-sign', 'font-size-semi-large', dir === 'ltr' ? 'en_sign' : 'ar_sign']"> {{ dir === 'ltr' ? 'm' : 'م' }} <sup>2</sup></span>
      </div>
      <div class="form-input">
        <label>
          {{ $t('property.room_numbers') }} <sup style="color: red">*</sup>
        </label>
        <div class="number-counter-input">
          <label>
            {{ $t('property.bedrooms') }}
          </label>
          <CustomCounter
            :step="1"
            :validation-name="$t('property.bedrooms')"
            validation-rules="required|min_value:1"
            v-model="property.form2.bedrooms"
            :min="1"
            :max="10" />
        </div>
        <div class="number-counter-input">
          <label for="bathrooms1">
            {{ $t('property.bathrooms') }}
          </label>

          <CustomCounter
            id="bathrooms1"
            :step="1"
            :validation-name="$t('property.bathrooms')"
            validation-rules="required|min_value:1"
            v-model="property.form2.bathrooms"
            :min="1"
            :max="10" />
        </div>
      </div>
      <div class="form-input">
        <label>
          {{ $t('property.beds_availability') }}
        </label>
        <div
          class="number-counter-input"
          v-for="(bedType, index) in propertyData.bedTypes"
          :key="index">
          <label class="mb-0">
            {{ bedType.name }}
          </label>
          <CustomCounter
            :step="1"
            :validation-name="'count'"
            validation-rules="required|min_value:0"
            v-model="property.form2.bedTypes[index].count"
            :min="0"
            :max="10" />
        </div>
      </div>
    </div>

    <div class="page-actions">
      <b-button @click="goBack">{{ $t('common.back') }}</b-button>
      <b-button variant="primary" @click="goNext">
        {{ $t('common.next') }}
      </b-button>
    </div>
  </div>
</template>

<script>
import ChipSelector from '@/components/shared/form/chipSelector';
import CustomCounter from '@/components/shared/form/CustomCounter';
import ProgressCircle from '@/components/postingRequestForm/ProgressCircle';

export default {
  name: 'AddPropertyPage2',
  props: {
    propertyData: {
      type: Object,
      required: true,
    },
  },
  components: {
    ChipSelector,
    CustomCounter,
    ProgressCircle,
  },
  transition: {
    name: 'slide-fade',
    mode: 'out-in',
  },
  data() {
    return {
      currentProperty: null,
      property: {
        form1: null,
        form2: {
          area: 100,
          furnishType: null,
          propertyType: null,
          bedrooms: 1,
          bathrooms: 1,
          bedTypes: [
            {
              property_bed_types_id: 1,
              count: 0,
            },
            {
              property_bed_types_id: 2,
              count: 0,
            },
            {
              property_bed_types_id: 3,
              count: 0,
            },
            {
              property_bed_types_id: 4,
              count: 0,
            },
          ],
        },
        form3: null,
        form4: null,
        form5: null,
        form6: null,
      },
    };
  },
  head() {
    return {
      title: `${this.dir === "ltr" ? "Add Property | Step 2" : "إضافة عقار | الخطوة 2"} ${this.appEnv !== 'production' ? '| Staging' : ''}`,
      meta: [
        // hid is used as unique identifier. Do not use `vmid` for it as it will not work
        {
          hid: 'description',
          name: 'description',
          content: this.dir === "ltr" ? 'Add Property | Property Details' : 'إضافة عقار | تفاصيل العقار'
        }
      ]
    }
  },
  methods: {
    updatePropertyType(item) {
      this.property.form2.propertyType = item;
    },
    updateFurnishType(item) {
      this.property.form2.furnishType = item;
    },
    goNext() {
      const { furnishType, propertyType, area } = this.property.form2;
      if (!furnishType?.length || !propertyType?.length) {
        window.scrollTo({
          top: 120,
          left: 0,
          behavior: 'smooth',
        });
        let body = this.$t('form.checkListFirst');
        this.$bvToast.toast(body, {
          title: this.$t('form.checkListFirst'),
          variant: 'primary',
          toaster: 'b-toaster-top-center',
          solid: true,
          autoHideDelay: 2000,
        });
        return;
      }
      if((area > 10000 || area < 0) && !!area){
        window.scrollTo({
          top: 120,
          left: 0,
          behavior: 'smooth',
          solid: true,
          autoHideDelay: 2000,
        });
        alert(area)
        let body = this.$t('property.propertyArea_Warning');
        this.$bvToast.toast(body, {
          title: this.$t('property.propertyArea_Warning'),
          variant: 'primary',
          toaster: 'b-toaster-top-center',
          solid: true,
          autoHideDelay: 1000,
        });
        return;
      }

      localStorage.setItem(
        'property',
        JSON.stringify({
          ...this.currentProperty,
          form2: this.property.form2,
        })
      );
      this.$router.push(this.localePath('/properties/add/3'));
    },
    goBack() {
      this.$router.push(this.localePath('/properties/add/1'));
    },
  },
  computed:{
    appEnv(){
      return this.$config.APP_ENV
    },
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr';
    },
  },
  mounted() {
    window.scrollTo({
      top: 0,
      left: 0,
      behavior: 'smooth',
    });
    this.currentProperty = localStorage.getItem('property') && JSON.parse(localStorage.getItem('property'));

    if (!this.currentProperty)
      this.$router.push(this.localePath('/properties/add/1'));
    if (this.currentProperty.form2) this.property = this.currentProperty;
  },
};
</script>

<style lang="scss">
.form-input.area {
  position: relative;
  .input-field {
    width: 120px !important;
  }
  .metre-sign {
    position: absolute;
    top: 33px;
    user-select: none;
    &.en_sign {
      right: 40px;
    }
    &.ar_sign {
      left: 20px;
    }
  }
}
</style>
